var that;

import uniPopup from '@/components/uni-popup/uni-popup.vue'
import uCharts from '@/components/u-charts/u-charts.js';

export default {
	components: {
		uniPopup
	},
	data() {
		return {
			sid: '',
			product: {},
			user: {},
			order:{},

			open: '0',
			current: '0',

			inx1: '0',
			inx2: '1',

			time: 60,
			money: '20',
			buyInx: '',
			bili: 1.3,
			endTime: ''
		}
	},
	onLoad(options) {
		that = this;
		that.sid = options.sid;

		that.getProduct();
		that.getUser();
		that.getEndTime();

		setInterval(function() {
			let timestamp = that.endTime.getTime(); //当前的时间戳
			timestamp = timestamp + 1000;
			that.endTime = new Date(timestamp);
		}, 1000);

		setInterval(function() {
			that.current = that.getRandom();
		}, 1000);

		
		
	},
	methods: {

		getProduct() {
			that.$utils.http(that.$api.product_info, {
				sid: that.sid
			}, function(res) {
				that.product = res;

				that.current = that.getRandom();
				that.open = that.getRandom();
				that.getServerData();
			});
		},

		getUser() {
			that.$utils.http(that.$api.get_user_info, function(res) {
				that.user = res;
			})
		},

		getServerData() {
			
			that.$utils.http(that.$api.kdata +that.product.filename,function(res){
				console.log(res)
			},{
				dataType:'html',
				type:'get'
			});
			
			

			// uni.request({
			// 	url: that.$constant.server_url + that.$api.kdata + that.product.filename,
			// 	dataType:'html',
			// 	success: function(res) {
			// 		console.log(res)
			// 	}
			// 	,
			// 	fail: (e) => {

			// 		console.log(e)

			// 		uni.hideLoading();
			// 		uni.showModal({
			// 			content: '服务器繁忙，请重试',
			// 			showCancel: false
			// 		});
			// 	}
			// });

		},

		createOrder() {
			that.$utils.http(that.$api.create_order, {
				name: that.name,
				time: that.time,
				money: that.money,
				price: that.current,
				inx: that.buyInx
			},function(res){
				that.order = res;
				that.$refs.buy.close();
				that.$refs.res.open();
			});
		},

		changeMoney(money) {
			that.money = money;
		},

		otherMoney(event) {
			that.money = event.target.value
		},

		getEndTime() {
			let timestamp = new Date().getTime(); //当前的时间戳
			timestamp = timestamp + 1000 * that.time;
			that.endTime = new Date(timestamp);
		},

		changeTime(time, bili) {
			that.time = time;
			that.bili = bili;
			that.getEndTime();
		},

		buy(buyInx) {
			that.buyInx = buyInx;
			//that.$refs.buy.open();
			that.$refs.res.open();
		},
		
		jxbuy(){
			that.$refs.res.close();
			that.$refs.buy.open();
		},
		
		change(inx) {
			that.inx1 = (that.inx1 + 1) % 2;
		},

		changeInx2(inx2) {
			that.inx2 = inx2;
		},

		getRandom() {
			var random = Math.random() * (that.product.max - that.product.min) + that.product.min;
			return Math.floor(random * 100000) / 100000;
		}
	}

}
